// pages/payParkingFee/payParkingFee.js
const context = require("../../context/Java110Context.js");
const constant = context.constant;
const util = context.util;

export default {
	data() {
		return {
			date: '2018-12-25',
			index: 0,
			active: 0,
			tablist: ['缴费', '历史'],
			TabCur: 0,
			scrollLeft: 0,
			showFeeMonth: false,
			feeMonthList: ['一个月', '半年', '一年', '两年'],
			feeMonthName: '一个月',
			feeMonth: 1,
			endTime: '',
			ordEndTime: '',
			amount: 0,
			receivableAmount: 0.00,
			communityId: '',
			communityName: '',
			feeId: '',
			floorNum: '',
			unitNum: '',
			roomNum: '',
			layer: '',
			builtUpArea: '',
			costList: [{}, {}], //费用清单
			additionalAmount: "",
			appId:''
		};
	},

	components: {},
	props: {},

	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		
		let accountInfo = uni.getAccountInfoSync();
		this.appId = accountInfo.miniProgram.appId;

		let _fee = JSON.parse(options.fee);
		console.log('_fee', _fee);
		let _amount = _fee.amount;
		let _receivableAmount = _amount ;
		let _communityInfo = context.getCurrentCommunity();
		let _lastDate = new Date(_fee.endTime);
		let _endTime = util.date.addMonth(_lastDate, this.feeMonth);

		this.receivableAmount = _receivableAmount;
		this.communityId = _communityInfo.communityId;
		this.communityName = _communityInfo.communityName;
		this.floorNum = _fee.floorNum;
		this.unitNum = _fee.unitNum;
		this.roomNum = _fee.roomNum;
		this.layer = _fee.layer;
		this.builtUpArea = _fee.builtUpArea;
		this.feeId = _fee.feeId;
		this.amount = _amount;
		this.additionalAmount = _fee.additionalAmount;
		this.endTime = util.date.formatDate(_endTime);
		this.ordEndTime = _fee.endTime;
	},

	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady: function() {},

	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {},

	/**
	 * 生命周期函数--监听页面隐藏
	 */
	onHide: function() {},

	/**
	 * 生命周期函数--监听页面卸载
	 */
	onUnload: function() {},

	/**
	 * 页面相关事件处理函数--监听用户下拉动作
	 */
	onPullDownRefresh: function() {},

	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {},

	/**
	 * 用户点击右上角分享
	 */
	onShareAppMessage: function() {},
	methods: {
	
	
		dateChange: function(e) {
			console.log("onConfirm", e);
			let _feeMonthName = null;
			_feeMonthName = this.feeMonthList[e.detail.value];;
			let _feeMonth = 1;

			if (_feeMonthName == '一个月') {
				_feeMonth = 1;
			} else if (_feeMonthName == '半年') {
				_feeMonth = 6;
			} else if (_feeMonthName == '一年') {
				_feeMonth = 12;
			} else if (_feeMonthName == '两年') {
				_feeMonth = 24;
			} else {
				return;
			}

			let _receivableAmount = _feeMonth * this.amount ;

			let _lastDate = new Date(this.ordEndTime);

			let _newDate = util.date.addMonth(_lastDate, _feeMonth);

			this.showFeeMonth = false;
			this.feeMonthName = _feeMonthName;
			this.receivableAmount = _receivableAmount;
			this.feeMonth = _feeMonth;
			this.endTime = util.date.formatDate(_newDate);
		},
		onFeeMonthChange: function(e) {
			console.log(e);
		},
		onFeeMonthCancel: function(e) {
			this.showFeeMonth = false;
		},
		_payWxApp:function(_data){
			let _receivedAmount = this.receivableAmount ;
			wx.showLoading({
				title: '支付中'
			});
			
			let _tradeType = 'APP';
			let _objData = {
				cycles: this.feeMonth,
				communityId: this.communityId,
				feeId: this.feeId,
				feeName: '物业费',
				receivedAmount: _receivedAmount,
				tradeType:_tradeType,
				appId:this.appId
			};
			context.request({
				url: constant.url.preOrder,
				header: context.getHeaders(),
				method: "POST",
				data: _objData,
				//动态数据
				success: function(res) {
					console.log(res);

					if (res.statusCode == 200 && res.data.code == '0') {
						let data = res.data; //成功情况下跳转
						let obj = {
							appid: data.appId,
							noncestr: data.nonceStr,
							package: 'Sign=WXPay', // 固定值，以微信支付文档为主
							partnerid: data.partnerid,
							prepayid: data.prepayid,
							timestamp: data.timeStamp,
							sign: data.sign // 根据签名算法生成签名
						}
						// 第二种写法，传对象字符串
						let orderInfo = JSON.stringify(obj)
						uni.requestPayment({
						    provider: 'wxpay',
						    orderInfo: orderInfo, //微信、支付宝订单数据
						    success: function (res) {
						       uni.showToast({
						       	title: "支付成功",
						       	duration: 2000
						       });
						       uni.navigateBack({});
						    },
						    fail: function (err) {
						        console.log('fail:' + JSON.stringify(err));
						    }
						});
						wx.hideLoading();
						return;
					}

					wx.hideLoading();
					wx.showToast({
						title: "缴费失败",
						icon: 'none',
						duration: 2000
					});
				},
				fail: function(e) {
					wx.hideLoading();
					wx.showToast({
						title: "服务器异常了",
						icon: 'none',
						duration: 2000
					});
				}
			});
		},
		onPayFee: function() {
			let _receivedAmount = this.receivableAmount ;
			wx.showLoading({
				title: '支付中'
			});
			let _tradeType = 'JSAPI';
			let _objData = {
				cycles: this.feeMonth,
				communityId: this.communityId,
				feeId: this.feeId,
				feeName: '物业费',
				receivedAmount: _receivedAmount,
				tradeType:_tradeType,
				appId:this.appId
			};
			context.request({
				url: constant.url.preOrder,
				header: context.getHeaders(),
				method: "POST",
				data: _objData,
				//动态数据
				success: function(res) {
					console.log(res);

					if (res.statusCode == 200 && res.data.code == '0') {
						let data = res.data; //成功情况下跳转
						uni.requestPayment({
							'timeStamp': data.timeStamp,
							'nonceStr': data.nonceStr,
							'package': data.package,
							'signType': data.signType,
							'paySign': data.sign,
							'success': function(res) {
								uni.showToast({
									title: "支付成功",
									duration: 2000
								});
								uni.navigateBack({});
							},
							'fail': function(res) {
								console.log('fail:' + JSON.stringify(res));
							}
						});
						wx.hideLoading();
						return;
					}

					wx.hideLoading();
					wx.showToast({
						title: "缴费失败",
						icon: 'none',
						duration: 2000
					});
				},
				fail: function(e) {
					wx.hideLoading();
					wx.showToast({
						title: "服务器异常了",
						icon: 'none',
						duration: 2000
					});
				}
			});
		}
	}
};
